/**
 * @author fanhualing 2018-11-12 查询出卡核实列表
 */
import { Component, OnInit } from "@angular/core";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { OutCardVerifyQueryBean } from "./bean/out-card-verify-query.bean";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CreditCcmService } from "app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service";
import { Router } from '@angular/router';
import * as _ from 'lodash';
import { TZB_CREL_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';

@Component({
    selector: 'outCardVerifyQuery',
    templateUrl: './out-card-verify-query.component.html',
    styleUrls: ['./out-card-verify-query.component.scss'],
    providers: [Commfunc, ConfirmationService, CreditCcmService]
})
export class OutCardVerifyQueryComponent implements OnInit {

    bean: OutCardVerifyQueryBean = new OutCardVerifyQueryBean();
    guaranteeMode: any = [];
    msgs: Message[];
    startDate: Date = null;
    endDate: Date = null;
    tablelist: any = [];
    first: number = 0;
    totalNum: number = 0;
    ch: any = CALENDAR_CN;
    orgTreeDisplay: boolean = false;
    ownerOrgInValue: any;
    openAcctOrgName: string;
    flagOrg: string;
    handleOrgName: string;
    personPage: boolean = false;
    userInValue: any;
    agentName: string;
    Display: boolean = false;//弹出框
    selectProductDisplay: boolean = false;//弹出框
    title: string;//弹出窗标题
    prodNo: string;//产品编号
    productName: string;//产品名称
    isCatelog: boolean = true;
    code: any; // 码值
    unionpayDisposeOutCardQuery: any = [];
    unionpayDisposeResultList: any = [];
    creditTypeList: any = [];
    stopFlag: boolean = true;
    startDateDt: Date = null;
    endDateDt: Date = null;
    subCreditTypeList: any = [];
    subList: any = [];
    creditTypeTableList: any = [];
    subDefaultList: any = [{ 'label': '请选择', value: '' }]

    constructor(
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        private creditCcmService: CreditCcmService,
        public router: Router,
    ) {
        this.codeValues();
        this.guaranteeMode = this.code['CardGuaranteeMode'];
        this.unionpayDisposeOutCardQuery = this.code['UnionpayDisposeOutCardQuery'];
        this.unionpayDisposeResultList = this.code['UnionpayDisposeResultQueryList'];
        this.creditTypeList = this.code['creditTypeList'];
        this.subList = this.code['subCreditTypeList'];
        this.subCreditTypeList = this.subDefaultList;
        this.creditTypeTableList = this.code['creditTypeTableList'];
    }

    // 获取码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        this.bean.pageNum = 1;
        this.bean.pageSize = 10;
        this.first = 0;
        // this.querySecle();
    }

    // 初始化表格
    querySecle() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.doQuery();
    }

    // 查询列表
    doQuery() {

        // 校验页面数据
        // 客户号
        if (this.bean.custNo == '') {
            this.bean.custNo = undefined;
        }
        // 客户名称
        if (this.bean.custName == '') {
            this.bean.custName = undefined;
        }
        // 身份证号
        if (this.bean.documentNo == '') {
            this.bean.documentNo = undefined;
        }
        // 申请码
        if (this.bean.creditBarCode == '') {
            this.bean.creditBarCode = undefined;
        }
        // 担保方式
        if (this.bean.guaranteeType == '') {
            this.bean.guaranteeType = undefined;
        }
        // 银联处理结果
        if (this.bean.unionpayStatus == '') {
            this.bean.unionpayStatus = undefined;
        }
        // 核实类型
        if (this.bean.creditType == '') {
            this.bean.creditType = undefined;
        }
        // 核实类型二级下拉框
        if (this.bean.creditTypeSecond == '') {
            this.bean.creditTypeSecond = undefined;
        }
        var reg = /^[0-9]*$/;
        var reg1 = /^[A-Za-z0-9]+$/;
        if (this.bean.custNo != undefined) {
            if (this.bean.custNo != '') {
                if (!reg.test(this.bean.custNo)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确客户号！' });
                    return
                }
                if (this.bean.custNo.length != 10) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入10位数字的客户号！' });
                    return
                }
            }
        }
        if (this.bean.custName != undefined) {
            if (this.bean.custName != '') {
                if (reg.test(this.bean.custName)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入正确客户名称！' });
                    return
                }
            }
        }
        var regNo = /^[0-9]*$/;
        // 日期处理,如果日期为空,则不传给后台
        if (this.startDate != undefined && this.startDate != null) {
            this.bean.tellerDateStart = this.commfunc.transDateN(this.startDate);
        } else {
            this.bean.tellerDateStart = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            this.bean.tellerDateEnd = this.commfunc.transDateN(this.endDate);
        } else {
            this.bean.tellerDateEnd = undefined;
        }
        if (this.startDateDt != undefined && this.startDateDt != null) {
            this.bean.startDate = this.commfunc.transDateN(this.startDateDt);
        } else {
            this.bean.startDate = undefined;
        }
        if (this.endDateDt != undefined && this.endDateDt != null) {
            this.bean.endDate = this.commfunc.transDateN(this.endDateDt);
        } else {
            this.bean.endDate = undefined;
        }
        // 默认-审批状态997
        this.bean.approvalStatus = '997';
        // 查询服务
        let ob = this.httpService.queryCreditOutCardCheckListInfo(this.bean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tablelist = data.resultList == null ? [] : data.resultList;
                this.totalNum = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }

    // 开始、结束日期判断
    showTime(param1, param2) {
        if (param1 == 'plan') {
            if (this.startDate && this.endDate) {
                if (this.startDate > this.endDate) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDate = null;
                        } else {
                            this.endDate = null;
                        }
                    })
                }
            }
        }
        if (param1 == 'outCard') {
            if (this.startDateDt && this.endDateDt) {
                if (this.startDateDt > this.endDateDt) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDateDt = null;
                        } else {
                            this.endDateDt = null;
                        }
                    })
                }
            }
        }
    }

    //选择机构
    openOrg(param) {
        this.flagOrg = param;
        this.orgTreeDisplay = !this.orgTreeDisplay;
        this.ownerOrgInValue = 'adjustOrg'
    }

    //机构树回调
    treeCall(param) {
        this.orgTreeDisplay = false;
        if (this.flagOrg == 'open') {
            this.openAcctOrgName = param.orgName;
            this.bean.tellerOrgNo = param.orgId;
        } else if (this.flagOrg == 'handle') {
            this.handleOrgName = param.orgName;
            this.bean.tellerOrgNo = param.orgId;
        }
    }

    //选择员工
    chooseWorker() {
        this.userInValue = '0000';
        this.personPage = !this.personPage;
    }

    //员工回调
    perInfor(item) {
        this.personPage = false;
        if (item.length > 0) {
            this.bean.tellerNo = item[0].tellerId;
            this.agentName = item[0].tellerName;
        }
    }

    //分页
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.doQuery();
    }

    // 重置
    reset() {
        this.bean = new OutCardVerifyQueryBean();
        this.agentName = undefined;
        this.handleOrgName = undefined;
        this.productName = undefined;
        this.startDate = null;
        this.endDate = null;
        this.startDateDt = null;
        this.endDateDt = null;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.subCreditTypeList = this.subDefaultList;
    }

    // 手动输入日期
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }

    // 关闭模态框
    onHideClose() {
        this.personPage = false;
        this.orgTreeDisplay = false;
    }
    // 选择产品
    choosePro() {
        this.selectProductDisplay = true;
    }

    // 关闭模态框
    closeSetting1() {
        this.selectProductDisplay = false;
    }

    // 选择产品回调
    productCall(event) {
        if (event && event.length > 0) {
            this.productName = event[0].productName;
            this.bean.productNo = event[0].productId;
        }
        this.selectProductDisplay = false;
    }

    // 查询关联页面
    toDetails(col) {
        // let post_name = JSON.parse(this.commfunc.getSessionData('commonHeader'))['extParam']['postId'];
        // const params = {
        //     'productNo': col.productNo,
        //     'roleIds': [post_name],
        // };
        let post_name = JSON.parse(this.commfunc.getSessionDataCH('postIdList'));
        const params = {
            'productNo': col.productNo,
            'roleId': post_name,
            'viewIndex': 1,
            'viewSize': 100,
        };
        this.commfunc.clearSession('viewId');
        this.creditCcmService.queryViewRelations(params).subscribe(datas => {
            if (datas.returnCode.code === 'success') {
                if (datas.list && datas.list.length > 0) {
                    let viewId = [];
                    datas.list.forEach(e => {
                        if (!e.viewName.match('卡要素')) {
                            viewId.push({ a: e.viewConfigId, b: e.viewName });
                        }
                    });
                    this.commfunc.setSessionDataCH('viewId', JSON.stringify(viewId));
                    let param, pageParams
                    for (let i = 0; i < datas.list.length; i++) {
                        let str = '卡要素';
                        if (datas.list[i].viewName.indexOf(str) == -1) {
                            this.stopFlag = false;
                            param = {
                                'viewConfigId': datas.list[i]['viewConfigId'],
                                'viewName': datas.list[i]['viewName'],
                            }
                            let viewConfigIds = datas.list[i]['viewConfigId'];
                            let viewNames = datas.list[i]['viewName'];
                            let paramse = {
                                loanCustRequestId: col.custRequestNo,//授信请求标识
                            };

                            pageParams = {
                                'viewConfigId': viewConfigIds,
                                'viewName': viewNames,
                                'custNo': col.custNo,
                                'custName': col.custName,
                                'custRequestNo': col.custRequestNo,
                                'productNo': col.productNo,
                                'productName': col.productName,
                                'isView': '',
                                'businessType': col.businessType,
                                'processId': col.processId,
                                'taskId': col.taskId,
                                'workCreater': col.workCreater,
                                'guaranteeType': col.guaranteeType,
                                'applyAmt': col.applyAmt,
                                'taskRate': col.taskRate,
                                'taskType': 'approve',
                                'flag': '2',
                                'varMap': {
                                    'taskType': 'approve',
                                    'dataMap': {
                                        'taskType': col.taskType,
                                    }
                                }
                            };
                            if (col.creditType == '0001' || col.creditType == '0007') {
                                pageParams['custRequestId'] = col.requestSeqNo;
                                param['ckhs'] = 'Y';
                                param['isout'] = 'Y';
                                this.commfunc.setSessionDataCH('creditDataWorkFlow', JSON.stringify(pageParams))
                                this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify(param)]);
                                break;
                            } else if (col.creditType == '0002' || col.creditType == '0005' || col.creditType == '0008') {
                                pageParams['marketingCampaignId'] = col.requestSeqNo;
                                this.commfunc.setSessionDataCH('creditDataWorkFlow', JSON.stringify(pageParams))
                                param['ckhs'] = 'Y';
                                param['isout'] = 'Y';
                                this.router.navigate(['/pages/tzb/custom/credit-ccm/viewPage', JSON.stringify(param)]);
                                break;
                            }
                        }
                    }
                    if (this.stopFlag) {
                        this.msgs = [];
                        this.msgs = [{ severity: 'info', summary: '提示', detail: '产品和岗位未关联页面,请配置关联页面' }];
                    }
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'info', summary: '提示', detail: '产品和岗位未关联页面,请配置关联页面' }];
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: datas.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }

    compileStr(cust) {
        return this.commfunc.compileStr(cust)
    }
    // 导出
    doExport() {
        let InParam = {};
        let InParamIn = {};
        InParam = _.clone(this.bean);
        if (InParam['commonHeader']) {
            delete InParam['commonHeader']
        }
        // 日期处理,如果日期为空,则不传给后台
        if (this.startDate != undefined && this.startDate != null) {
            InParam['tellerDateStart'] = this.commfunc.transDateN(this.startDate);
        } else {
            InParam['tellerDateStart'] = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            InParam['tellerDateEnd'] = this.commfunc.transDateN(this.endDate);
        } else {
            InParam['tellerDateEnd'] = undefined;
        }
        // 默认-审批状态997
        InParam['approvalStatus'] = '997';
        InParamIn['condition'] = InParam;
        InParamIn['exportType'] = '01';

        this.httpService.creditAdjustExport(InParamIn).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let aa = data.fileName;
                window.open(TZB_CREL_DOWNLOAD + "?fileName=" + encodeURI(aa) + "&fileUrl=" + data.fileUrl)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
    }

    // 更改一级核实类型
    changeFirstType() {
        this.bean.creditTypeSecond = undefined;
        if (this.bean.creditType == '00017') {
            this.subCreditTypeList = this.subList;
        } else {
            this.subCreditTypeList = this.subDefaultList;
        }
    }
}