import { ModuleWithProviders } from "@angular/core";

export const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'out-card-verify-query',component: OutCardVerifyQueryComponent},
        ]
    }

]

export const OutCardVerifyRouting: ModuleWithProviders = RouterModule.forChild(routes);